@import "index", "highlight";

.archive {
  font-size: 1.1em; }

.archive time {
  display: inline-block;
  min-width: 10ch;
  margin: 0 .2em; }



.categories {
  color: $link-color;
  margin-bottom: .15em; }







.hidden {
  display: none; }





.icons svg {
  height: 1em;
  width: 1em;
  fill: currentColor;
  transition: .2s color;
  vertical-align: middle;
  margin-bottom: .15em; }





.more {
  margin: 2em 0 1em; }

.more a {
  border-radius: 2px;
  border: 1.5px solid $link-color;
  padding: .4em .8em;
  transition: .2s; }

.more a:hover {
  color: #fff;
  background: $link-color;
  text-decoration: inherit; }





.related div {
  flex: 1 0 15em; }

.related span {
  display: block;
  color: reduce(100); }

.related a {
  font-size: 1.2em;
  font-weight: bold; }

@media (min-width: 30em) {
  .next {
    text-align: right; } }





.title {
  font-size: 1.5em;
  width: 100%;
  margin: .1em 0 .6em; }





.caption {
  display: block;
  margin-top: 0.5em;
  text-align: center;
  font-size: 0.9em; }

.figure {
  padding: 0;
  margin: 2em; }

.katex {
  font-size: 1.0em !important; }



ol.bibliography li {
    list-style-type: none;
    margin-left: 0px;
    padding-left: 0px; }

ol.bibliography {
    margin-left: 0px;
    padding-left: 0px; }
